.loader-container {
  text-align: center;
  min-height: calc(100vh - 60px);
  overflow: auto;
  position: relative;
  padding-top: 60px;
}

.loader-title {
  font-size: 25px;
  font-weight: 400;
  color: rgba(0,0,0,0.70);
  margin-top: 50px; 
}

.graf-bg-container {
  width: 100%;
  height: 310px;
  overflow: hidden;
  perspective: 2000px;
  opacity: .7
}

.graf-layout {
  height: 100%;
  margin: auto;
  position: relative;
  perspective: 2000px;
}

.graf-circle:first-child {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%) rotateY(1000deg) rotateX(1000deg) rotate(0deg);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 5px solid #2098f3;
  animation: scaleOne 5.5s infinite alternate linear;
  opacity: 0
}

@keyframes scaleOne {
  to {
      border-radius: 50%;
      transform: translate(-50%,-50%) rotateY(180deg) rotateX(90deg) rotate(1000deg);
      opacity: 0
  }
}

.graf-circle:nth-child(2) {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%) rotateX(135deg) rotateY(135deg) rotate(0deg);
  width: 310px;
  height: 310px;
  border-radius: 50%;
  border: 5px solid #ffa20a;
  border-left: none;
  border-top: none;
  animation: scaleTwo 5s infinite alternate linear
}

@keyframes scaleTwo {
  to {
      border-radius: 50%;
      transform: translate(-50%,-50%) rotateX(135deg) rotateY(135deg) rotate(1turn);
      opacity: 0
  }
}

