@import url(https://fonts.googleapis.com/css?family=Lato:300,400|Roboto:300,400,500);
.Social {
  margin: 0 auto;
  display: block;
}

.Social ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.Social li {
  display: inline;
  margin: 0 1em 0 0;
}

.Social a {
  color: #212121;
  text-decoration: none;
  font-size: 1.2em;
}
.About-name, .About-profession, .About-location   {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.About {
  text-align: center;
}

.About-avatar {
  padding: .5em 0 0 0;
}

.About-avatar img {
  border-radius: 100%;
  width: 10rem;
  height: 10rem;
  border: 0.2rem solid #dd4814;
  margin: 0 auto;
  display: block;
  box-shadow:  0 0 10px  rgba(0,0,0,0.6);
}

.About-name h2 {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  letter-spacing: 1.2px;
  margin: .5em 0 0 0;
  color: #dd4814;
}

.About-profession p {
  margin: .2em 0 1em 0;
  letter-spacing: 1.6px;
  font-weight: 300;
  color: #dd4814;
}

.About-desc {
  color: #757575;
  font-size: 1em;
  font-weight: 400;
  margin: 0 0 1em 0;
}

.About-location {
  font-size: 1em;
  font-weight: 400;
  color: #757575;
  margin: 0 0 1em 0;
}

.About-contact {
  font-size: 1em;
  font-weight: 400;
  color: #757575;
  margin: 0 0 1em 0;
}

.About-contact span {
  margin-left: 0.2rem;
}

.Education {
  margin-bottom: 2rem;
}

.Experience {
  margin-bottom: 2rem;
}

.Skills {
  margin-bottom: 2rem;
}
.Languages {
  margin-bottom: 2rem;
}
.loader-container {
  text-align: center;
  min-height: calc(100vh - 60px);
  overflow: auto;
  position: relative;
  padding-top: 60px;
}

.loader-title {
  font-size: 25px;
  font-weight: 400;
  color: rgba(0,0,0,0.70);
  margin-top: 50px; 
}

.graf-bg-container {
  width: 100%;
  height: 310px;
  overflow: hidden;
  -webkit-perspective: 2000px;
          perspective: 2000px;
  opacity: .7
}

.graf-layout {
  height: 100%;
  margin: auto;
  position: relative;
  -webkit-perspective: 2000px;
          perspective: 2000px;
}

.graf-circle:first-child {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%) rotateY(1000deg) rotateX(1000deg) rotate(0deg);
          transform: translate(-50%,-50%) rotateY(1000deg) rotateX(1000deg) rotate(0deg);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 5px solid #2098f3;
  -webkit-animation: scaleOne 5.5s infinite alternate linear;
          animation: scaleOne 5.5s infinite alternate linear;
  opacity: 0
}

@-webkit-keyframes scaleOne {
  to {
      border-radius: 50%;
      -webkit-transform: translate(-50%,-50%) rotateY(180deg) rotateX(90deg) rotate(1000deg);
              transform: translate(-50%,-50%) rotateY(180deg) rotateX(90deg) rotate(1000deg);
      opacity: 0
  }
}

@keyframes scaleOne {
  to {
      border-radius: 50%;
      -webkit-transform: translate(-50%,-50%) rotateY(180deg) rotateX(90deg) rotate(1000deg);
              transform: translate(-50%,-50%) rotateY(180deg) rotateX(90deg) rotate(1000deg);
      opacity: 0
  }
}

